import logo from './logo.svg';
import './App.css';
import Note from './components/Note'
import { useState, useEffect } from 'react'
import { logRoles } from '@testing-library/react'
import axios from 'axios'
import noteService from './services/notes'
/*const Note = ({ note }) => {
  return (
    <li>{note.content}</li>
  )
};*/

const Notification = ({ message }) => {
  if (message == null) {
    return null
  }
  return (
    <div className='error'>
      {message}
    </div>
  )
}
const Footer = () => {
  const footstyle = {
    color: 'green',
    fontstyle: 'italic',
    fontsize: 16
  }
  return (
    <div style={footstyle}>
      <br />
      <em>Note app, Department of Computer Science, University of Helsinki 2022</em>
    </div>
  )
}
const App = () => {
  console.log('At starting');
  const [notes, setNotes] = useState(null);
  const [newNote, setNewNote] = useState(
    'a new note...'
  )
  const [showAll, setShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState('some error happened...')
  useEffect(() => {
    console.log('effect');
    noteService.getAll()
      .then(
        initialNotes => {
          setNotes(initialNotes)
        }
      )
  }, []
  )
  //console.log('render', notes.length, 'notes')
  const handleNoteChange = (event) => {
    console.log(event.target.value);
    setNewNote(event.target.value);
  }
  const toggleImportance = (id) => {
    console.log('importance of ' + id + ' needs to be toggled')
    console.log(`importance of ' + ${id} + ' needs to be toggled`)
    const url = `http://localhost:3001/notes/${id}`
    const note = notes.find(
      (n) => {
        return id == n.id
      }
    )
    const changedNote = { ...note, important: !note.important }
    noteService.update(id, changedNote).then(
      (returnedNote) => {
        setNotes(notes.map(n => n.id != id ? n : returnedNote))
      }
    ).catch(
      error => {
        setErrorMessage(`Note '${note.content}' was already deleted from server`)
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
        setNotes(notes.filter(
          n => n.id !== id
        ))
      }
    )
    /*axios.put(url, changedNote).then(
      response => {
        setNotes(notes.map(n => n.id != id ? n : response.data))
      }
    )*/
  }
  const addNote = (event) => {
    event.preventDefault();
    const noteObject = {
      content: newNote,
      important: Math.random() < 0.5
    }
    noteService.create(noteObject).then(
      returnedNote => {
        setNotes(notes.concat(returnedNote))
        setNewNote('')
      }
    )
    /*axios
      .post('http://localhost:3001/notes', noteObject)
      .then(
        response => {
          console.log(response);
          setNotes(notes.concat(noteObject))
          setNewNote('')
        }
      )*/
    console.log('button clicked', event.target);
  }
  const notesToShow = showAll ? notes : notes.filter(
    note => note.important
  )
  if (!notes) {
    return null
  }
  return (
    <div>
      <h1> Notes </h1>
      <Notification message={errorMessage} />
      <button onClick={() => {
        setShowAll(!showAll);
      }}>
        show {showAll ? 'Important' : 'All'}
      </button>
      <ul>
        {notesToShow.map(
          (note, i) => {
            console.log('value of i ', i);
            return <Note key={note.id} note={note} toggleImportance={() => toggleImportance(note.id)}
            />
            //return <li key={i}>{note.content}</li>
          }
        )}
      </ul>
      <form onSubmit={addNote}>
        <input value={newNote} onChange={handleNoteChange} />
        <button type='submit'>Save</button>
      </form>
      <Footer />
    </div>
  );
}

export default App;
